:root {
    --primary-color: #019c74;
    --secondary-color: #000;
    --background-color: #f4f4f4;
    --form-background: #fff;
    --form-border: #ddd;
    --box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .contactContainer {
    max-width: 500px;
    margin: 0 auto;
    padding: 2rem;
    background-color: var(--background-color);
  }
  
  h1 {
    text-align: center;
    color: var(--primary-color);
    margin-bottom: 2rem; /* Add margin below the title to push the form down */
  }
  
  form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .formContainer {
    padding: 1rem;
    border: 1px solid var(--form-border);
    background-color: var(--form-background);
    box-shadow: var(--box-shadow);
    border-radius: 1rem; 
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 100%;
    margin-top: 2rem; /* Moves the form down */
    max-height: 500; /* Adjusts the maximum height of the container */
    overflow-y: auto; /* Allows scrolling if content overflows */
  }
  
  label {
    margin-bottom: 0.5rem;
    color: var(--secondary-color);
  }
  
  input,
  textarea {
    width: 100%;
    padding: 0.8rem;
    border: 1px solid var(--form-border);
    border-radius: 0; /* Keeps the input fields rectangular */
    font-size: 1rem;
  }
  
  textarea {
    resize: vertical;
    min-height: 100px;
  }
  
  button {
    background-color: var(--primary-color);
    color: #fff;
    padding: 0.8rem;
    border: none;
    cursor: pointer;
    border-radius: 20px; 
    transition: background-color 0.3s;
  }
  
  button:hover {
    background-color: var(--primary-color);
  }
  
  p {
    text-align: center;
    color: var(--secondary-color);
  }
  